@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;900&display=swap);
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}a{text-decoration:none;color:inherit}a:hover{font-style:italic}body{margin:0;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:black;color:white}@media (prefers-color-scheme: dark){body{background-color:black;color:white}}@media (prefers-color-scheme: light){body{background-color:white;color:black}}

.container{height:90vh;width:100vw;text-align:center;display:flex;flex-direction:column;justify-content:center}.container .title{font-family:"Source Sans Pro", sans-serif;font-size:8rem;font-weight:900;margin-bottom:50px}.container .menu{font-family:"Times New Roman", Times, serif;font-size:1.6rem}.container .menu>*{margin-right:13px}.container .menu>*:hover{-webkit-transform:translateY(100px) !important;transform:translateY(100px) !important}.container .menu:last-child{margin-right:0}

